import React, {useState} from 'react';

export default function DraftItem ({draft, remove, save}) {
    const [value, setValue] = useState(draft.text);
    console.log(draft.id);
    function handleText(event) {
        setValue(event.target.value);
    }

    function toSaveHandler(text, id) {
        text ? save(text) : save('Text')
        remove(id)
    }

    return(
        
        <li className="draft__item" >
<textarea cols="30" rows="3" placeholder="Text" value={value} className="draft__card-text" onChange={handleText}></textarea>
                <div className="draft__buttons">
                    <button className="button draft__remove-btn" onClick={()=> remove(draft.id)}>Remove</button>
                    <button className="button draft__save-btn" onClick={()=> toSaveHandler(value, draft.id)}>Save</button>
                </div>
                
        </li>
    )
}