import React from 'react';
import DraftItem from './DraftItem';

export default function DraftList(props) {
    return(
        <ul className="draft__list list ">
            {props.drafts.map((draft) => {
                return(
                    <DraftItem 
                        draft={draft} 
                        remove={props.toDelete}
                        save={props.toSave} 
                        key={draft.id}
                        />
                )
            })}
        </ul>
    )
}