import React from 'react';
import CardItem from './CardItem';

export default function MainList(props) {
    return(
        <ul className="cards__list list">
            {props.cards.map((card) => {
                return(
                    <CardItem 
                        card={card} 
                        draft={props.toDraft}
                        mark={props.toMark} 
                        remove={props.toDelete}
                        key={card.id}
                        />
                )
            })}
        </ul>
    )
}