import React from 'react';

export default function CardItem({card, draft,mark, remove}) {
    function toDraftHandler(id, text) {
        draft(text);
        remove(id)
    }
    const classes = ['card__indicator'];
    card.colorGreen ? classes.push('green') : classes.push('red')
    return(
        <li className="card__item">
            <div className="card__header">
                <p className="card__text">{card.text}</p>
                <div className={classes.join(' ')}></div>
            </div>
            <div className="card__footer">
                <button className="button card__btn-draft" onClick={() => toDraftHandler(card.id,card.text)}>Draft</button>
                <button className="button card__btn-mark" onClick={() => mark(card.id)}>Mark</button>
            </div>
        </li>
    )
}