import React, {useState} from 'react';
import Form from './components/Form';
import DraftList from './components/DraftList';
import MainList from './components/MainList';

export default function App() {
    const [cards, setCards] = useState([]);
    const [drafts, setDrafts] = useState([]);

    function createCard(text) {
        setCards(
            cards.concat([
                {
                text,
                id: `f${(~~(Math.random()*1e8)).toString(16)}`,
                colorGreen: false
            }
            ])
        )
    }

    function createDraft(text) {
        setDrafts(
            drafts.concat([
                {
                    text,
                    id: `f${(~~(Math.random()*1e8)).toString(16)}`
                }
            ])
        )
    }

    function removeDraft(id) {
        setDrafts(
            drafts.filter(card => card.id !==id)
        )
    }

    function removeCard(id) {
        setCards(
            cards.filter(card => card.id !==id)
        )
    }

    function changeColor(id) {
        setCards(
            cards.map(card => {
                if (card.id === id) {
                    card.colorGreen = !card.colorGreen
                }
                return card
            })
        )}
    
    function countGreen(array) {
        let counter = 0;
         array.forEach((item) => {
            if (item.colorGreen) 
            counter++ 
         })
         return counter
    }

    return ( 
        <div className = "main">
            <header className='header'>
                <div className="header__wrap">
                    <h1 className="header__title">
                        React app for <span className="header__title  header__title_av">Aventica</span>
                    </h1>
                    <div className="header__counter">
                        <p className="header__number">
                            {countGreen(cards)}
                        </p>
                    </div>
                </div>
            </header>
            <div className = "container">
                <div className="aside">
                    <Form toMain={ createCard } toDraft={ createDraft } />
                    <div className="draft">
                        <h2 className="draft__title">Draft</h2>
                        <DraftList drafts = {drafts} toSave={ createCard } toDelete={ removeDraft } />
                    </div>
                </div>
                <div className="cards__section">
                    <MainList cards={cards} toDraft={createDraft} toMark={changeColor} toDelete={ removeCard }/>
                </div>
            </div>
        </div>          
            
        
    )
}