import React, { useState } from 'react'



export default function ({ toMain, toDraft }) {
    const [value, setValue] = useState('')
    
    function handleChange(event) {
        setValue(event.target.value)
    }

    function handleDraft(event) {
        event.preventDefault()
        toDraft(value)
        setValue('')
    }

    function handleSave(event) {
        event.preventDefault()
        value ? toMain(value) : toMain('Text')
        setValue('')
    }

    
        return (
            <form className = "form">
               <input type="text" value={value} onChange={handleChange} className="form__input" placeholder = "Input" />
                <div className="form__buttons">
                    <button onClick = {handleDraft} className="button form__draft-btn">Draft</button>
                    <button onClick = {handleSave} className="button form__save-btn">Save</button>
                </div>
            </form>
        )   
    }